* {
    box-sizing: border-box;
}

@font-face {
    font-family: "centurygothic";
    src: url("./font/centurygothic.ttf");
}

@font-face {
    font-family: "BebasNeue";
    src: url("./font/BebasNeue.ttf");
}

body {
    margin: 0;
    font-size: 16px;
    max-width: 1920px;
    /* line-height: Line-height(24)/font-size(16)=1.5 */
    line-height: 1.5;
    letter-spacing: 0.02em;
    box-sizing: border-box;
}

main{
    overflow-x: hidden;
}

:root {
    --main-font: "centurygothic";
    --second-font: "BebasNeue";

    --white: #FFF;
    --black: #242424;
    --brown: #C3A488;
    --brown-hover: #AD825C;
    --grey: #5A5869;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

p:last-child {
    margin-bottom: 0;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    color: currentColor;
    text-decoration: none;
}

button {
    cursor: pointer;
    outline: none;
    border: none;
    background-color: transparent;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

input,
button,
textarea,
select {
    font: inherit;
    border: none;
    outline: none;
}

.h-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.container {
    max-width: 1270px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
    box-sizing: border-box;
}



.header {
    position: fixed;
    width: 100%;
    z-index: 5;
    transition: all 0.3s ease;
}

.header__scrolled {
    background-color: var(--brown);
}


.header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 40px;
    transition: all 0.3s ease;
}

.header__style {
    padding: 15px 0;
}

.logotype {
    max-width: 145px;
}

.header__rht {
    display: flex;
    gap: 76px;
    align-items: center;
}

.header__menu {
    display: flex;
    gap: 76px;
}

.menu__item {
    font-family: var(--second-font);
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    color: var(--white);
}

.menu__item::after {
    content: '';
    display: block;
    height: 1px;
    width: 0;
    margin: 0 auto;
    background-color: var(--white);
    opacity: 0;
    transition: all 0.3s ease;
}

.menu__item:hover::after {
    opacity: 1;
    width: 100%;
}

.header__burger {
    width: 22px;
    cursor: pointer;
}

.burger__item {
    height: 1px;
    background-color: var(--white);
    width: 22px;
    margin-bottom: 5px;
}

.burger__item:last-child{
    margin: 0;
}

.burger__item_short {
    width: 18px;
    margin-left: auto;
}



.hero {
    position: relative;
}

.hero__video {
    width: 100%;
    max-width: 1920px;
    height: calc(100vh + 100px);
    object-fit: cover;
    pointer-events: none;
}

.play__video {
    width: 55px;
    height: 55px;
    margin: 0 auto;
    margin-bottom: 20px;
}

.video-link {
    font-size: 18px;
    font-family: var(--main-font);
    text-align: center;
    color: var(--white);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.video-link::after{
    content: '';
    display: block;
    width: 0;
    height: 1px;
    background-color: var(--white);
    opacity: 0;
    margin: 0 auto;
    transition: all 0.3s ease;
}

.video-link:hover::after{
    opacity: 1;
    width: 100%;
}

.modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    cursor: pointer;
    z-index: 10;
}

.modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    max-width: 1700px;
}

.close-modal {
    position: absolute;
    top: -31px;
    right: -20px;
    font-size: 25px;
    color: #fff;
    cursor: pointer;
}

#videoModal video {
    max-width: 1700px;
    width: 100%;
    height: auto;
}

.modal__menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: var(--brown);
    z-index: 9;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    overflow: auto;
}

.modal__menu_open{
    opacity: 1;
    visibility: visible;
}

.modal__menu_content{
    padding: 40px 140px;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.modal__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 70px;
}

.modal__pages{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.pages__item{
    font-family: var(--second-font);
    color: var(--white);
    font-size: 35px;
    text-align: center;
    position: relative;
    cursor: pointer;
    line-height: 100%;
}
.pages__item {
    width: fit-content;
}

.pages__item a:hover::after{
    width: 100%;
}

.pages__item a::after{
    content: '';
    display: block;
    width: 0;
    height: 1px;
    margin: 0 auto;
    background-color: var(--white);
    transition: all 0.3s ease;
    margin-top: 5px;
}

.modal__logo{
    max-width: 145px;
}

.pages__item__list{
    text-align: center;
    margin-top: 40px;
}

.pages__list_item{
    font-family: var(--main-font);
    color: var(--white);
    font-size: 20px;
    line-height: 100%;
    margin-bottom: 20px;
    cursor: pointer;
}

.modal__footer{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.schedule__item{
    font-family: var(--main-font);
    font-size: 18px;
    color: var(--white);
    margin-bottom: 15px;
}

.schedule__item:last-child{
    margin-bottom: 0;
}

.footer__city{
    display: flex;
    gap: 15px;
}

.city__item{
    font-family: var(--main-font);
    font-size: 18px;
    color: var(--white);
}

.modal__social{
    display: flex;
    gap: 15px;
}

.modal__menu_close{
    cursor: pointer;
}



